"use client"

import Link from "next/link"
import { useState, useEffect, useRef } from "react"
import { Menu, X, ChevronDown } from "lucide-react"

export default function Navbar() {
  const [isOpen, setIsOpen] = useState(false)
  const [scrolled, setScrolled] = useState(false)
  const [activeLink, setActiveLink] = useState("/")
  const [dropdownOpen, setDropdownOpen] = useState(false)
  const dropdownRef = useRef(null)
  const triggerRef = useRef(null)

  useEffect(() => {
    const handleScroll = () => setScrolled(window.scrollY > 20)
    window.addEventListener("scroll", handleScroll)
    return () => window.removeEventListener("scroll", handleScroll)
  }, [])

  // Close dropdown when clicking outside
  useEffect(() => {
    const handleClickOutside = (event) => {
      if (
        dropdownRef.current && 
        !dropdownRef.current.contains(event.target) &&
        triggerRef.current &&
        !triggerRef.current.contains(event.target)
      ) {
        setDropdownOpen(false)
      }
    }

    document.addEventListener("mousedown", handleClickOutside)
    return () => document.removeEventListener("mousedown", handleClickOutside)
  }, [])

  const navLinks = [
    { href: "/", label: "Home" },
    { href: "/about", label: "About" },
  ]

  const services = [
    {
      title: "Video Editing",
      href: "/services/video-editing",
    },
    {
      title: "Web Development",
      href: "/services/web-development",
    },
    {
      title: "Digital Marketing",
      href: "/services/digital-marketing",
    },
  ]

  return (
    <nav className={`fixed top-0 left-0 right-0 z-50 transition-all duration-300 ${scrolled ? "py-2" : "py-4"}`}>
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div
          className={`flex justify-between items-center transition-all duration-300 rounded-full ${
            scrolled
              ? "glassmorphic px-6 py-3 shadow-lg border border-[#1B3C53]/20"
              : "px-6 py-4 hover:glassmorphic hover:shadow-sm transition-all"
          }`}
        >
          <Link href="/" className="flex items-center gap-3 flex-shrink-0 group">
            <div className="w-8 h-8 md:w-10 md:h-10 rounded-lg bg-gradient-to-br from-[#234C6A] to-[#1B3C53] flex items-center justify-center font-bold text-white transition-all duration-300 group-hover:scale-110 group-hover:shadow-lg">
              ◆
            </div>
            <span
              className={`font-bold text-[#1B3C53] hidden sm:inline transition-all duration-300 ${
                scrolled ? "text-lg" : "text-xl"
              }`}
            >
              DROPLE
            </span>
          </Link>

          {/* Desktop Navigation */}
          <div className="hidden md:flex items-center gap-6">
            {navLinks.map((link) => (
              <Link
                key={link.href}
                href={link.href}
                onClick={() => {
                  setActiveLink(link.href)
                  setDropdownOpen(false)
                }}
                className={`px-4 py-2 rounded-full text-sm font-medium transition-all duration-300 ${
                  activeLink === link.href 
                    ? "text-white bg-[#234C6A] shadow-md" 
                    : "text-[#1B3C53] hover:bg-[#234C6A]/10 hover:text-[#1B3C53]"
                }`}
              >
                {link.label}
              </Link>
            ))}

            {/* Services Dropdown */}
            <div className="relative" ref={dropdownRef}>
              <button
                ref={triggerRef}
                onClick={() => setDropdownOpen(!dropdownOpen)}
                onMouseEnter={() => setDropdownOpen(true)}
                className={`px-4 py-2 rounded-full text-sm font-medium flex items-center gap-1 transition-all duration-300 ${
                  dropdownOpen 
                    ? "text-white bg-[#234C6A] shadow-md" 
                    : "text-[#1B3C53] hover:bg-[#234C6A]/10 hover:text-[#1B3C53]"
                }`}
              >
                Services
                <ChevronDown 
                  className={`w-3 h-3 ml-1 transition-transform duration-300 ${dropdownOpen ? "rotate-180" : ""}`} 
                />
              </button>

              {/* Small Horizontal Dropdown */}
              <div
                className={`absolute top-full left-0 mt-2 backdrop-blur-xl rounded-xl shadow-lg border border-[#234C6A]/20 
                transition-all duration-300 ease-out transform z-50 ${
                  dropdownOpen
                    ? "opacity-100 translate-y-0 pointer-events-auto"
                    : "opacity-0 -translate-y-1 pointer-events-none"
                }`}
                onMouseLeave={() => setDropdownOpen(false)}
              >
                <div className="p-3 glass-card min-w-[200px]">
                  <div className="flex flex-col space-y-1">
                    {services.map((service) => (
                      <Link
                        key={service.href}
                        href={service.href}
                        onClick={() => setDropdownOpen(false)}
                        className="px-3 py-2 text-sm text-[#1B3C53] hover:text-white hover:bg-[#234C6A] rounded-lg transition-all duration-200 flex items-center justify-between group"
                      >
                        <span>{service.title}</span>
                        <span className="opacity-0 group-hover:opacity-100 transition-opacity duration-300">→</span>
                      </Link>
                    ))}
                  </div>
                </div>
                
                {/* Small arrow */}
                <div className="absolute -top-1 left-4 w-2 h-2 bg-white border-l border-t border-[#234C6A]/20 transform rotate-45"></div>
              </div>
            </div>
          </div>

          {/* CTA Button */}
          <div className="hidden md:flex items-center gap-3">
            <Link
              href="/get-started"
              onClick={() => setDropdownOpen(false)}
              className="px-6 py-2.5 rounded-full bg-gradient-to-r from-[#234C6A] to-[#1B3C53] text-white font-semibold text-sm hover:shadow-lg hover:scale-105 transition-all duration-300 active:scale-95"
            >
              Get Started
            </Link>
          </div>

          {/* Mobile Menu Button */}
          <button
            onClick={() => {
              setIsOpen(!isOpen)
              setDropdownOpen(false)
            }}
            className="md:hidden p-2 text-[#1B3C53] hover:text-[#234C6A] transition-colors duration-200"
            aria-label="Toggle menu"
          >
            {isOpen ? <X size={24} /> : <Menu size={24} />}
          </button>
        </div>

        {/* Mobile Menu (unchanged) */}
        {isOpen && (
          <div className="md:hidden mt-4 p-5 glass-card backdrop-blur-xl rounded-3xl border border-[#234C6A]/20 space-y-3 animate-fade-in-down">
            {/* ... Mobile menu code ... */}
          </div>
        )}
      </div>
    </nav>
  )
}